// components/Profile.tsx
"use client";
import { useGetProfileQuery } from "@/redux/api/profile";
import { useRouter } from "next/navigation";

const Profile = () => {
  const { data: profile, isLoading, error } = useGetProfileQuery();
  const router = useRouter();

  if (isLoading) return <div>Loading...</div>;
  if (error) {
    // Если нет профиля, перенаправляем на страницу входа
    router.push("https://3-gymnasium.kg/accounts/login");
    return null;
  }

  return (
    <div>
      <h1>Профиль</h1>
      <p>Пользователь: {profile?.user}</p>
      {profile?.avatar && <img src={profile.avatar} alt="Аватар" />}
      <p>О себе: {profile?.about}</p>
    </div>
  );
};

export default Profile;
